<template>
    <div class="concerned_singer_box" :class="[$store.state.theme == 'night' ? 'concerned_singer_box_night':'']">
        <div class="concerned_singer_title">
            <div class="concerned_singer_title_div">
                <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 30 30">
                <path id="路径_183" data-name="路径 183" d="M95.357,86.879a6.786,6.786,0,0,1,1.409.147l-.238-1.565a8.385,8.385,0,0,0-1.171-.083,8.249,8.249,0,1,0,8.167,7.08l-1.565-.238a6.751,6.751,0,1,1-6.6-5.341Zm4,1.462-3.8,3.8a1.551,1.551,0,0,0-.2-.014,1.492,1.492,0,0,0-1.485,1.7l-.011.011.014.015a1.5,1.5,0,0,0,1.254,1.254l.014.014.011-.011a1.55,1.55,0,0,0,.2.014,1.5,1.5,0,0,0,1.5-1.5,1.437,1.437,0,0,0-.015-.2l3.8-3.8,4.409.672,5.3-5.3-5.547-.794-1.141,1.136,4.109.561-3.187,2.953-4.008-.468-.586-4.1,2.977-2.836.68,3.89,1.18-1.18-.84-5.522-5.3,5.3Zm9.16,2.284A13.487,13.487,0,1,1,98.361,80.465l1.23-1.23A14.984,14.984,0,1,0,109.752,89.4Z" transform="translate(-80.358 -78.629)" fill="#5ab4f0"/>
            </svg>
                <span>关注</span>
            </div>
        </div>
        <div class="concerned_singer_body">
        <Singer :Singer-data="concerned_singer_data"></Singer>
        <div class="Placeholder"></div>
        </div>
        <div>
            <Menu1 :menu_show="4"></Menu1>
        </div>
        <div style="position: fixed; top:3.5rem;left:1rem;z-index: 9">
            <Back :color="1"></Back>
        </div>
    </div>
</template>

<script>
    import Menu1 from "../../components/Menu1";
    import Back from '../../components/menu/Back'
    import Singer from "../../components/home/Singer";
    import ajax from "../../http"
    export default {
        name: "ConcernedSinger",
        components: {Singer,Back,Menu1},
        data(){
            return{
                concerned_singer_data:[]
            }
        },
        mounted() {
            let id = this.$route.params.id
            ajax(
                this.$store.state.api_url+'user/follows',
                {uid:id,cookie:this.$store.state.user_cookie}
            ).then((res)=>{
                console.log(res);
                for (let i = 0; i<res.follow.length;i++){
                    this.concerned_singer_data.push({
                        singer_image:res.follow[i].avatarUrl,
                        singer_name:res.follow[i].nickname,
                        user_id:res.follow[i].userId,
                        account_gender:res.follow[i].gender
                    })
                }
            }).catch((err)=>{
                console.log(err);
            })
        }
    }
</script>

<style scoped>
.concerned_singer_box{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    display: flex;
    flex-direction: column;

}
    .concerned_singer_title{
        width: 100%;
        height: 6rem;
        display: flex;
        justify-content: center;

    }
    .concerned_singer_title_div{
        display: flex;
        margin-top: 3.5rem;
        align-items: center;
    }
    .concerned_singer_title span{
        font-size: 20px;
        font-weight: bold;
        margin-left: 0.5rem;
    }
    .concerned_singer_body{
        width: 100%;
        height: 100%;
        overflow-y: auto;
    }


    .concerned_singer_box_night{
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        display: flex;
        flex-direction: column;
        background-color: #222225;
        color: white;

    }
</style>